/* Global Styling */
body {
  font-family: 'Inter', sans-serif; /* Use Inter font */
  margin: 0;
  padding: 0;
  background-color: #0a192f; /* Dark background */
  color: #ccd6f6; /* Light text */
}

.container {
  display: flex;
  min-height: 100vh; /* Full height */
  width: 100vw; /* Full viewport width */
  max-width: 1200px; /* Set a maximum width for the container */
  margin: 0 auto; /* Center the container horizontally */
}

.left-column {
  width: 50%; /* Keep at 50% */
  padding: 2rem;
  background-color: #112240; /* Slightly lighter background */
  color: white;
  display: flex;
  flex-direction: column; /* Stack children vertically */
  justify-content: flex-start; /* Align children to the top */
  align-items: flex-start; /* Align children to the start */
  position: sticky; /* Make the left column sticky */
  top: 0; /* Stick to the top */
  height: 100vh; /* Full height of the viewport */
}

.right-column {
  width: 50%; /* Set to 50% */
  padding: 2rem;
  background-color: #0a192f; /* Dark background */
  height: auto; /* Full height of the viewport */
  overflow-y: visible; /* Allow vertical scrolling */
  text-align: right;
}




/* Adjust the BerkanInformation class */
.BerkanInformation {
  width: 80%; /* Limit width of the section */
  max-width: 600px; /* Set a maximum width if needed */
  margin: 0 auto; /* Center it */
  padding: 1rem; /* Add some padding */
}

.Name {
  text-align: left;
  width: 100%;
  font-size: 48px;
  margin-bottom: 1rem;
}

/* Typography */
h1 {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
}

h2 {
  font-size: 2.1rem;
  color: whitesmoke;
}

p {
  font-size: 1rem;
  margin-bottom: 1rem;
  line-height: 1.6;
}

/* Align Projects and Experience */
.right-column .projects, .right-column .experience {
  text-align: right;
  margin-bottom: 1rem;
}

/* Responsive Styling */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center; /* Align everything centered on mobile */
  }

  .left-column, .right-column {
    width: 100%;
  }
}

