}

/* Ensure the experience section fits within the container */
.Experience {
  padding: 2rem 1rem;
}

.container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.experience-content {
  width: 100%;
  max-width: 600px; /* Ensure it fits within the layout */
  padding: 1vh 0px;
}

.experience-card {
  width: 100%;
  height: auto;
  border-radius: 8px;
  padding: 20px; /* Adjusted padding */
  margin: 10px 0; /* Adjusted margin */
  text-align: right;
  color: #fff;
  text-decoration: none;
  background-color: transparent;
  transition: transform 0.2s ease-in-out;
}


.experience-card:hover {
  transform: scale(1.05);

}

.experience-title {
  font-size: 1.25rem;
  color: #38bdf8; /* Highlight color */
  margin-bottom: 8px;
}

.experience-details {
  font-size: 0.9rem;
  color: #b0b3b8;
}

.experience-description {
  font-size: 0.95rem;
  margin-bottom: 16px;
}

.tech-stack span {
  display: inline-block;
  background-color: #38bdf8;
  color: #1e293b;
  padding: 5px 10px;
  border-radius: 4px;
  margin-right: 8px;
  margin-top: 8px;
}

.ExperienceContainer {
  width: 100%;
  height: 100%;
  text-align: right;
}

.world-clock {
  display: flex;                  /* Use flexbox for horizontal alignment */
  justify-content: space-around;  /* Space out the clocks evenly */
  align-items: center;           /* Center align items vertically */
  flex-wrap: wrap;               /* Allow wrapping on smaller screens */
  padding: 2px;                 /* Add some padding around the container */
}

.clock {
  display: flex;                 /* Use flexbox for alignment */
  flex-direction: column;        /* Align icon and text vertically */
  align-items: center;           /* Center align the contents */
  margin: 0 10px;               /* Adjust margin for spacing */
  font-size: 0.56rem;            /* Make text smaller by 4px from the previous size */
}

.icon {
  font-size: 1.3rem;             /* Reduce icon size by 4px */
}

.ExactTime {
  font-size: 1 rem;
}

html {
  scroll-padding-top: 4rem; /* Adjust this value to create the space */
}
